html, body {
  padding: 0;
  margin: 0;
  height: 100%;
  font-size: 14px;
}
.draw-area {
    position: relative;
    width:100%;
    height:calc( 100vh - 100px );
    margin:40px auto;
}
svg {
    z-index: 0;
}
.vnode {
    position: absolute;
    z-index: 100;
    top: 10px;
    left: 10px;
    height: 160px;
    margin: 0;
    margin-top: -10px;
    font-size: 13px;
    line-height: 20px;
    border-radius: 3px;
    background: #FFFFFF;
    -webkit-box-shadow: 1px 2px 10px 3px rgba(0,0,0,0.08);
            box-shadow: 1px 2px 10px 3px rgba(0,0,0,0.08);;
    -webkit-transition: top .3s;;
    transition: top .3s;
    cursor: pointer;
    width: auto ;
    /* width: 230px;
    overflow:hidden; */
}
.vnode .text {
    padding: 0 10px;
}
.pnode::after {
    content: "";
    position: absolute;
    bottom: 0;
    left:0;
    display: inline-block;
    width: 216px;
    height: 1px;
    background: #E1244E;
}
.link {
    fill-opacity: 0;
}
.tip {
    z-index: 999;
    display: block;
    padding: 5px;
    border: 1px solid #eee;
    line-height: 20px;
    border-radius: 5px;
    background: transparent;
}
.vnode:hover {
    z-index: 999;
}
.vnode:hover .tip {
    display: block;
}
/**ruler部分*/
.ruler-head {
    font-size: 14px;
    text-align: left;
}
.ruler-head  span {
    display: inline-block;
    width: 50px;
    text-align: center;
}
.ruler {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 100px;
    margin-top: 10px;
    margin-left: 10px;
}
.ruler-ruler {
    position: relative;
    right: 10px;
    left: 5px;
    display: inline-block;
    width: 10px;
    height: 100px;
    background: #9a9797;
}
.ruler-deg {
    position: absolute;
    top: 50px;
    left: 0;
    width: 0;
    width: 0;
    height: 10px;
    margin-top: -5px;
    padding-left: 3px;
    border-left: 20px solid #666;
    font-size: 10px;
    line-height: 10px;
    text-align: center;
    color: #666;
    background: transparent;
    cursor: row-resize;
    -webkit-user-select: none; /* Chrome/Safari/Opera */
       -moz-user-select: none; /* Firefox */
            -ms-user-select: none;
        user-select: none;

    -webkit-touch-callout: none; /* iOS Safari */
}
.vnode .text {
  position: relative;
  height: 100%;
  max-width: 230px;
}
.vnode .text .node_title {
